import React from "react";
import { Router, Route, Link } from "react-router-dom";
import { createHashHistory } from 'history';
import casa from './casa.ico';
import mac from './mac.ico';
import account from './account.ico';
import modificaI from "./impiegato.ico";
import modificaP from "./parcheggio.ico";
import { dim, undim } from "../../actions/index";
import { useDispatch, useSelector } from 'react-redux';
import Home from "../home/home";
import Come from "../comeiscriversi/come";
import Orari from "../oraridilavoro/orario";
import Chi from "../chisiamo/chi";

const customHistory = createHashHistory();

function Navbar() {
    const dime = useSelector(state => state.D);
    const dispatch = useDispatch();
    /* Menù laterale */
    var dimensione = dime ? (
        <ul id="side" className="sidebar">
            <br></br>
            <li className="closebtn"><p onClick={() => dispatch(undim())}>×</p></li>
            <br></br>
            <br></br>
            <li>
                <img src={casa} alt="icona casa"/><Link to="/" onClick={() => dispatch(undim())}>Home</Link>
            </li>
            <br></br>
            <li>
                <img src={account} alt="icona account"/><Link to="/chi" onClick={() => dispatch(undim())}>Chi siamo</Link>
            </li>
            <br></br>
            <li>
                <img src={mac} alt="icona macchina"/><Link to="/come" onClick={() => dispatch(undim())}>Come iscriversi</Link>
            </li>
            <br></br>
            <li>
                <img src={modificaP} alt="icona account"/><a onClick={() => dispatch(undim())} target="_blank" rel="noreferrer" href="https://www.google.it/maps/place/Autoscuola+Barbaro/@38.1204872,13.3407452,19z/data=!4m13!1m7!3m6!1s0x1319ef41f2a099cd:0x7eced396ac079abe!2sVia+Serradifalco,+1,+90138+Palermo+PA!3b1!8m2!3d38.1207383!4d13.3407398!3m4!1s0x1319ef41f0c566e3:0x8e83feea465bf1b7!8m2!3d38.1204251!4d13.3410037">Dove ci troviamo</a>
            </li>
            <br></br>
            <li>
                <img src={modificaI} alt="icona account"/><Link to="/orari" onClick={() => dispatch(undim())}>Orari di lavoro</Link>
            </li>
        </ul>) : (null)
    return (
    <div>
        <Router history={customHistory}>
            <button className="openbtn" onClick={() => dispatch(dim())}>☰</button>
            {dimensione} 
            <Route exact path="/" component={Home}/>
            <Route exact path="/chi" component={Chi}/>
            <Route exact path="/come" component={Come}/>
            <Route exact path="/orari" component={Orari}/>
            </Router>
    </div>    
    )
}

export default Navbar;