import React from "react";
import rb from "./rb.jpg";

function Chi() {
    return (
        <div>
            <h1 id="h">Chi siamo:</h1>
            <figure>
                <img id="img3" src={rb} alt="Immagine Istruttore Barbaro Roberto"/>
            </figure>
            <p id="p7">Insegnante ed istruttore di scuolaguida. Competenza ed efficienza. Il personale sarà sempre a vostra disposizioni per qualsiasi eventuale dubbio. Inoltre non avrete nessuna difficoltà nella comunicazione con il docente. Si tratteranno gli argomenti in modo chiaro e sicuro, ripetendo, se necessario, i concetti necessari al conseguimento della padronanza degli argomenti affrontati nel corso.</p>
        </div>
    )
}

export default Chi;