import React from "react";
import gruppo from "./gruppo.jpg";

function Home() {
    return (
        <div>
            <h1 id="h">Cosa facciamo:</h1>
            <figure>
                <img id="img2" src={gruppo} alt="Immagine gruppo Autoscuola Barbaro"/>
            </figure>
            <p id="p1">Sarete preparati al meglio per affrontare l'esame di teoria. Lo studio metodico della materia e la disponibilità di svolgere il corso teorico in due lezioni al giorno, una la mattina e una il pomeriggio, vi aiuteranno a raggiungere quel tipo di conoscenza che vi permetterà di superare l'esame a quiz senza nessun problema. In secondo luogo la nostra competenza vi permetterà di superare anche l'esame di pratica in tutta sicurezza. Otterrete il tipo di patente più vicino alle vostre esigenze e potrete finalmente guidare il veicolo che desiderate.</p>
        </div>
    )
}

export default Home;