import React from "react";
import carta from "./carta.jpg";

function Come() {
    return (
        <div>
            <h1 id="h">Come iscriversi:</h1>
            <figure>
                <img id="img5" src={carta} alt="Immagine Carta d'Identità"/>
            </figure>
            <p id="p7"></p>
            <ul id="ul1">
                <li><p>Cosa devo avere per potermi iscrivere:</p>
                    <ul>
                        <li>quattro foto da tessera</li>
                        <li>fotocopia carta d'identità</li>
                        <li>fotocopia tessera sanitaria</li>
                        <li>amnestico del medico curante</li>
                    </ul>
                </li>
            </ul>
        </div>
    )
}

export default Come;